* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.color-switcher__container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.color-switcher__container button {
  width: 150px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border: none;
  border-radius: 5px;
  font-family: sans-serif;
  font-size: large;
}

.color-switcher__container button:not(:last-child) {
  margin-right: 20px;
}

.timer__container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.timer__container input {
  width: 310px;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px 5px;
  font-size: large;
}

.timer__container button {
  width: 100px;
  height: 30px;
  cursor: pointer;
  background-color: #0ff;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 3px 7px;
  font-size: large;
  transition: scale .25s linear;
}

.timer {
  display: flex;
}

.field {
  width: 80px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 50px;
}

